function FontTitle (props){
    let data=props.propsValue;
    return(
        <div className="title_area">
                    <div className="top">
                        <h2 className="title">Font</h2>
                    </div>
                    <div className="bottom">
                        <p>{data}</p>
                    </div>
                </div>
    );
}
export default FontTitle