import Header from "./component/Header";
import Start from "./component/main/Start";
import About from "./component/main/About";
import Skill from "./component/main/Skill";
import Project from "./component/main/Project";
import Footer from "./component/Footer";
import { useEffect } from "react";
import { useLocation } from "react-router-dom";
import gsap from "gsap";
import ScrollToPlugin from "gsap/ScrollToPlugin";


function Main() {
    let location = useLocation();
    
    useEffect(()=>{
        gsap.registerPlugin(ScrollToPlugin);
        let pageList=[];
        let section=document.querySelectorAll("section");
        let footer=document.getElementById("footer");
        let targety=0;
        for(let i=0; i<section.length; i++){
            pageList.push(section[i]);
            }
            pageList.push(footer);
            
        if(location.state){
            targety=pageList[location.state.id].offsetTop;
            gsap.to(window, {scrollTo: targety, duration: 0});
        }
        else{
            gsap.to(window, {scrollTo: 0, duration: 0});
        }
    });

return (
    <div id="main">
        <Header />
        <Start />
        <About />
        <Skill />
        <Project />
        <Footer />
    </div>
);
}

export default Main;
