import AboutContentList from "./AboutContentList";
import { useSelector } from "react-redux";

function AboutContent(){
    let data=useSelector(state => state.about);
    return(
        <div className="contents">
        <div className="profile">
            <h3>{data.profile.label}</h3>
            <AboutContentList propsValue={data.profile.list}/>
        </div>
        <div className="education">
            <h3>{data.education.label}</h3>
            <AboutContentList propsValue={data.education.list}/>
        </div>
        <div className="certificate">
            <h3>{data.certificate.label}</h3>
            <AboutContentList propsValue={data.certificate.list}/>
        </div>
    </div>
    );
}
export default AboutContent;