import Header from "./component/Header";
import Footer from "./component/Footer";
import Overview from "./component/detail/Overview";
import Font from "./component/detail/Font";
import Color from "./component/detail/Color";
import Mobile from "./component/detail/Mobile";
import { useEffect } from "react";
import { useParams } from "react-router-dom";
import { useSelector } from "react-redux";
import gsap from "gsap";
import ScrollToPlugin from "gsap/ScrollToPlugin";

function Detail(){
    let {path} = useParams();
    
    path = path-1;
    let dataList=useSelector(state => state.projects);
    let data=dataList[path];
    useEffect(()=>{
        gsap.registerPlugin(ScrollToPlugin);
        gsap.to(window, {scrollTo: 0, duration: 0})
    });
    return(
        <div id="detail">
            <Header />
            <Overview propsValue={data}/>
            <Font propsValue={data.font}/>
            <Color propsValue={data.color} />
            <Mobile propsValue={data.mobile} />
            <Footer />
        </div>
    );
}
export default Detail;