import FontContent from "./FontContent";
import FontTitle from "./FontTitle";

function Font(props){
    let data=props.propsValue;
    return(
        <section id="sec2">
            <div className="inner">
                <FontTitle propsValue={data.desc}/>
                <FontContent propsValue={data}/>
            </div>
        </section>
    );
}
export default Font;