import TextCirlce from '../TextCircle'
function Mobile(props){
    let data=props.propsValue;
    return(
        <section id="sec4">
            <div className="inner">
                <div className="title_area">
                    <h3>MOBILE</h3>
                    <p>Responsive Design</p>
                </div>
                <div className="contents">
                    <ul className="visuals">
                        {data.map((d, i)=><li key={i+i}><img src={`${process.env.PUBLIC_URL}/assets/images/`+d.image} alt={d.alt} /></li>)}
                    </ul>
                    <TextCirlce />
                </div>
                
            </div>
        </section>
    );
}
export default Mobile;