import { useSelector } from "react-redux";

function SubProjectTitle(props){
let dataList=useSelector(state => state.subProjectTitle);
let data;
if(props.propsValue === "openSource"){
    data=dataList.openSource;
    
}
else{
    data=dataList.caseStudy;
}

    return(
        <h2 className="title">{data.title}</h2>
    );
}
export default SubProjectTitle;