import { useSelector } from "react-redux";

function SkillContentList(){
    let data=useSelector(state => state.skill);
    return(
        <ol>
            {data.map((d, i)=>
                <li key={i+1}>
                        <h3>{d.h3}</h3>
                        <div className="logos">
                            {d.logo.map((d,i) => {
                                if(d.image){
                                    return <img key={i+1} src={`${process.env.PUBLIC_URL}/assets/images/`+d.image} alt={d.alt} />
                                }
                                else if(d.span){
                                    return <span key={i+1}>{d.span}</span>
                                }
                                return null
                            })}
                        </div>
                        <p className="desc">{d.desc}</p>
                    </li>
            )}
        </ol>
    );
}
export default SkillContentList;