import { useSelector } from "react-redux";

function StartTitle(){
    let data=useSelector(state => state.startTitle);
    return(
        <div className="title_area">
                <div className="title">
                    <h2 className="top">{data.title.top} <span></span></h2>
                    <h2 className="bottom">{data.title.bottom}</h2>
                </div>
                <div className="desc">
                    <span>
                        <span>{data.desc.span1}&nbsp;</span>
                        <strong>{data.desc.strong}&nbsp;</strong>
                        <span>{data.desc.span2}</span>
                    </span>
                </div>
            </div>
    );
}
export default StartTitle;