import DetailTitle from "./DetailTitle";
import OverviewContent from "./OverviewContent";
import OverviewThumb from "./OverviewThumb";

function Overview(props){
    let data=props.propsValue;
    return(
        <section id="sec1">
            <div className="inner">
                <DetailTitle propsValue={data.h3}/>
                <OverviewThumb propsValue={data} />
                <OverviewContent propsValue={data.overview}/>
            </div>
        </section>
    );
}
export default Overview