import { useSelector } from "react-redux";

function AboutTitle(){
    let data=useSelector(state => state.aboutTitle);
    return(
        <>
            <h2 className="title">{data.h2}</h2>
            <div className="desc">
                <p>{data.desc.p}</p>
                <strong>{data.desc.strong}</strong>
            </div>
        </>
    );
}
export default AboutTitle;