function ColorContent(props){
    let data=props.propsValue;
    return(
        <div className="contents">
            {data.map((d, i)=><div key={i+1} className={"color"+(i+1)} style={{backgroundColor:(d.hex)}}>
                        <div className="top">
                            <p>{d.index}</p>
                            <p>{d.name}</p>
                        </div>
                        <div className="bottom">
                            <p>{d.hex}</p>
                        </div>
                    </div>)}
                </div>
    );
}
export default ColorContent;