import { useSelector } from "react-redux";

function FooterLink(){
    let data=useSelector(state => state.footerLink);
    
    return(
        <div className="link">
            <ul>
                {data.map((d, i)=> <li key={i+1}><a href={d.href} target="contact">{d.label}</a></li>)}
            </ul>
            <a href="https://github.com/khj980415" className="github" target="contact"><img src={`${process.env.PUBLIC_URL}/assets/images/logo_github_gray.svg`} alt="github"/></a>
        </div>
    );
}
export default FooterLink;