import Copyright from "./Copyright";
import FooterLink from "./FooterLink";
import FooterTitle from "./FooterTitle";

function Footer(){
    return(
        <footer id="footer">
            <div className="inner">
                <div className="top">
                    <FooterTitle />
                    <FooterLink />
                </div>
                <Copyright />
            </div>
        </footer>
    );
}
export default Footer;