import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/pagination';
import { useSelector } from "react-redux";

function OpenSourceContent (){
    let data = useSelector(state => state.openSource);
    return(
        <Swiper 
                        slidesPerView={1}
                        breakpoints={{
                            920: {
                                slidesPerView: 3,
                                spaceBetween: 30,
                            }
                        }}
                        pagination={{
                            type: "progressbar"
                        }}
                        modules={[Pagination]}
                        className="swiper-container">
                            {data.map((d, i)=><SwiperSlide key={i+1}>
                            <a href={d.href} target="opensource">
                                        <div className="thumb">
                                            <img src={"./assets/images/"+d.image} alt={d.alt} />
                                        </div>
                                        <div className="desc">
                                            <h3>{d.h3}</h3>
                                            <ul>
                                                <li>
                                                    <span className="title">{d.list.props}</span>
                                                    <span>{d.list.value}</span>
                                                </li>
                                                <li>
                                                    {d.desc}
                                                </li>
                                            </ul>
                                        </div>
                                    </a>
                            </SwiperSlide> )}
                        </Swiper>
    );
}
export default OpenSourceContent;