function OverviewThumb(props){
    let data = props.propsValue;
    return(
        <div className="thumb_section">
                    <div className="top">
                        <div className="visual"><img src={`${process.env.PUBLIC_URL}/assets/images/`+data.thumb.image} alt={data.thumb.alt} /></div>
                    </div>
                    <div className="bottom">
                        <div className="desc">
                            <ul>
                                {data.list.map((d, i)=><li key={i+1}>
                                    <span className="title">{d.props}</span>
                                    <span>{d.value}</span>
                                </li>)}
                            </ul>
                        </div>
                        <div className="link"><a href={data.site} className="button" target="project">SITE</a></div>
                    </div>
                </div>
    );
    }
export default OverviewThumb;