function OverviewContent(props){
    let data=props.propsValue;
    return(
        <>
        <div className="desc">
            {data.desc.map((d, i)=><p key={i+1}>{d}</p>)}
        </div>
        <div className="overview">
            <div className="title_area">
                <h3>OVERVIEW</h3>
            </div>
            <div className="visual"><img src={`${process.env.PUBLIC_URL}/assets/images/`+data.image} alt={data.alt} /></div>
        </div>
        </>
    );
}
export default OverviewContent;