import { useSelector } from "react-redux";

function ListTitle(props){
    let dataList=useSelector(state => state.listTitle);
    let data;
    if(props.propsValue === "skill"){
        data=dataList.skill;
    }
    else{
        data=dataList.project;
    }
    return(
        <div className="title_area">
            <h2 className="title">{data.h2}</h2>
            <p>{data.p}</p>
        </div>
    );
}
export default ListTitle;