import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

function MainProjectContentList(){
    let data=useSelector(state => state.projects);
    
    return(
        <div className="contents">
        <ul>
            {
                data.map((d, i)=><li key={i+1}>
                <h3>{d.h3}</h3>
                <p>{d.p}</p>
                <div className="bottons">
                    <a href={d.site} target="project" className="button">SITE</a>
                    <Link to={`/projects/${i+1}`} className="button">STYLE GUIDE</Link>
                </div>
            </li>)
            }
        </ul>
        </div>
    );
}
export default MainProjectContentList;