import ColorContent from "./ColorContent";
import ColorTitle from "./ColorTitle";

function Color(props){
    let data = props.propsValue;
    return(
        <section id="sec3">
            <div className="inner">
                <ColorTitle propsValue={data.desc}/>
                <ColorContent propsValue={data.list}/>
            </div>
        </section>
    );
}
export default Color;