import Logo from "./Logo";
import Navigation from "./Navigation";
import { useEffect } from "react";
import ScrollTrigger from '@terwanerik/scrolltrigger'
import gsap from "gsap";
import ScrollToPlugin from "gsap/ScrollToPlugin";
import { useNavigate } from "react-router-dom";

function Header(){
    let navigate=useNavigate();
    useEffect(()=>{
    gsap.registerPlugin(ScrollToPlugin);
    let cursor=document.querySelector(".mouse_cursor");
    let header=document.getElementById("header");
    let nav=header.lastElementChild;
    let navLi=nav.firstElementChild.children;
    let pageList=[];
    let section=document.querySelectorAll("section");
    let footer=document.getElementById("footer");
    let targety=0;

    let winW=0;
    let device;

    for(let i=0; i<section.length; i++){
        pageList.push(section[i]);
        }
        pageList.push(footer);
        

        document.body.addEventListener("mousemove", function(e){
        gsap.to(cursor, {left: e.pageX-5, top: e.pageY-5, duration: 0.2})
        });

    function cursorDisplay(){
        winW=window.innerWidth;
        if(winW>760){
            device="pc";
        }
        else{
        device="mobile";
        }
        if (device === "mobile"){
            if(!document.body.classList.contains("mobile")){
                document.body.classList.add("mobile");
            }
        }
        else{
            if(document.body.classList.contains("mobile")){
                document.body.classList.remove("mobile");
            }
        }
        }

    cursorDisplay();
    
    window.addEventListener("resize", function(){
    cursorDisplay();
    });

        

    let projectsPage=document.getElementById("projects");
    let detailPage=document.getElementById("detail");

    if(!projectsPage && !detailPage){
        let trigger=new ScrollTrigger({
            trigger: { //     
                once: true,
                offset: {
                    viewport: {
                    x: 0,
                    y: 0.25 //  3/4  
                    }
                },
                toggle: {
                    class: {
                    in: "active",
                    out: "inactive"
                    }
                },
                },
            scroll: { //   JavaScript   
                    callback: offset => scrollInteraction(offset.y)
                }
            });
            
            trigger.add("section, footer");
    
            let n=0;
            
            function scrollInteraction(t){
            if(t<pageList[1].offsetTop){
                n=0;
            }
            else if (t < pageList[2].offsetTop){
                n=1;
            }
            else if (t < pageList[3].offsetTop){
                n=2;
            }
            else if (t < pageList[4].offsetTop){
                n=3;
                if( window.innerHeight+t === document.body.scrollHeight){
                n=4;
                }
            }
            else {
                n=4;
            }
    
            for(let i=0; i<navLi.length; i++){
                if(i===n){
                if(!navLi[i].firstElementChild.classList.contains("active")){
                    navLi[i].firstElementChild.classList.add("active");
                }
                }
                else{
                if(navLi[i].firstElementChild.classList.contains("active")){
                    navLi[i].firstElementChild.classList.remove("active");
                }
                }
            }
            
            }
            
            for(let i=0; i<navLi.length; i++){
            navLi[i].addEventListener("click", function(e){
                e.preventDefault();
                targety=pageList[i].offsetTop;
                gsap.to(window, {scrollTo: targety, duration: 0.5});
            });
        }

    }
    else{
        let trigger=new ScrollTrigger({
            trigger: { //     
                once: true,
                offset: {
                    viewport: {
                    x: 0,
                    y: 0.25 //  3/4  
                    }
                },
                toggle: {
                    class: {
                    in: "active",
                    out: "inactive"
                    }
                },
                },
            });
            
            trigger.add("section, footer");

            navLi[3].firstElementChild.classList.add("active");

            for(let i=0; i<navLi.length; i++){
                if(i < navLi.length-1){
                    if(i === 3){
                        if(!detailPage){
                        navLi[i].addEventListener("click", function(e){
                            e.preventDefault();
                            gsap.to(window, {scrollTo: 0, duration: 0.5});
                        })}
                        else {
                            navLi[i].addEventListener("click", function(e){
                                e.preventDefault();
                                navigate("/projects");
                            });
                        }
                    }
                    else {
                        navLi[i].addEventListener("click", function(e){
                            e.preventDefault();
                            navigate("/", {state: {id: i}});
                        });
                    }
                }
                else {
                    navLi[i].addEventListener("click", function(e){
                        e.preventDefault();
                        targety=pageList[pageList.length-1].offsetTop;
                        gsap.to(window, {scrollTo: targety, duration: 0.5});
                    });
                }
                

            }
    }
    });

    return(
        <>
        <div className="mouse_cursor"></div>
        <header id="header">
            <Logo />
            <Navigation />
        </header>
        </>
    );
}
export default Header;