function FontContent(props){
    let data=props.propsValue;
    return(
        <div className="contents">
                    <div className="kor" style={{fontFamily: (data.kor.fontFamily)}}>
                        <ul className="sample">
                            {data.kor.weight.map((d, i)=><li key={i+1} className={d}>
                                {data.kor.label}
                            </li>)}
                        </ul>
                        <ul className="font">
                        {data.kor.weight.map((d, i)=><li key={i+1} className={d}>
                                {data.kor.name[i]}
                            </li>)}
                        </ul>
                    </div>
                    <div className="eng" style={{fontFamily: (data.eng.fontFamily)}}>
                        <ul className="sample">
                            {data.eng.weight.map((d, i)=><li key={i+1} className={d}>
                                {data.eng.label}
                            </li>)}
                        </ul>
                        <ul className="font">
                        {data.eng.weight.map((d, i)=><li key={i+1} className={d}>
                                {data.eng.name[i]}
                            </li>)}
                        </ul>
                    </div>
                </div>
    );
}
export default FontContent